import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import 'rxjs/add/operator/filter'
import { Observable } from 'rxjs/Observable';
import { FriggResponse, RoleDto, RoleService } from '../../../proxies';

@Injectable()
export class RoleResolverService implements Resolve<RoleDto> {
  constructor(private roleService: RoleService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<RoleDto> {
    let roleId: number;

    if (route.params.id === 'new') {
      roleId = 0;
    } else {
      roleId = route.params.id as number;
    }

    return this.roleService.getRole(roleId)
      .filter((response: FriggResponse<RoleDto>) => {
        return response.success;
      })
      .map((response: FriggResponse<RoleDto>) => {
        return response.result;
      });
  }
}



