import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CoreService } from '../../core/core.service';
import { FriggResponse, ListResultDto, UserListDto, UserService } from '../../proxies';

@Component({
  selector: 'frigg-user-list',
  templateUrl: 'user-list.component.html',
  styleUrls: [ 'user-list.component.scss' ],
})
export class UserListComponent implements OnInit {
  users: Array<UserListDto> = [];

  constructor(private core: CoreService,
              private userService: UserService,
              private router: Router) {
  }

  ngOnInit(): void {
    this.core.setTranslatedShellHeader('frigg.users');
    this.core.setTranslatedNavigateText('frigg.homePage');
    this.core.shell.setNavigateUrl('/dashboard');

    this.userService.getUserList()
      .subscribe((response: FriggResponse<ListResultDto<UserListDto>>) => {
        if (response.success) {
          this.users = response.result.items;
        }
      });
  }

  onItemClick(user: UserListDto): void {
    this.router.navigate([ `./admin/users/${ user.id }` ]);
  }
}



