import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CoreService } from '../../core/core.service';
import { RoleListDto } from '../../proxies';

@Component({
  selector: 'frigg-role-list',
  templateUrl: 'role-list.component.html',
  styleUrls: [ 'role-list.component.scss' ]
})
export class RoleListComponent implements OnInit {
  roles: Array<RoleListDto> = [];

  constructor(private core: CoreService,
              private route: ActivatedRoute,
              private router: Router) {
  }

  ngOnInit(): void {
    this.core.setTranslatedShellHeader('frigg.roles');
    this.core.setTranslatedNavigateText('frigg.homePage');
    this.core.shell.setNavigateUrl('/dashboard');

    this.route.data.subscribe((data: any) => {
      this.roles = data.roles;
    })
  }

  onItemClick(role: RoleListDto): void {
    this.router.navigate([ `./admin/roles/${ role.id }` ]);
  }
}



