import { NgModule } from '@angular/core';
import { SharedModule } from '../shared/shared.module';
import { AdminRoutingModule, routedComponents } from './admin.routing';
import { RoleResolverService } from './role-detail/resolver/role-resolver.service';
import { RoleDetailComponent } from './role-detail/role-detail.component';
import { RoleListResolverService } from './role-list/resolver/role-list-resolver.service';
import { RoleListComponent } from './role-list/role-list.component';
import { RoleTileComponent } from './role-list/tile/role-tile.component';
import { UserDetailComponent } from './user-detail/user-detail.component';
import { UserTileComponent } from './user-list/tile/user-tile.component';
import { UserListComponent } from './user-list/user-list.component';


@NgModule({
  imports: [
    SharedModule,
    AdminRoutingModule
  ],
  declarations: [
    routedComponents,
    UserDetailComponent,
    UserListComponent,
    UserTileComponent,
    RoleDetailComponent,
    RoleListComponent,
    RoleTileComponent
  ],
  exports: [
    UserListComponent,
    RoleListComponent
  ],
  providers: [
    RoleResolverService,
    RoleListResolverService
  ]
})
export class AdminModule {
}



