import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { CoreService } from '../../core/core.service';
import { FriggResponse, RoleDto, RoleService } from '../../proxies';

@Component({
  selector: 'frigg-role-detail',
  templateUrl: 'role-detail.component.html',
  styleUrls: [ 'role-detail.component.scss' ]
})
export class RoleDetailComponent implements OnInit {
  role: RoleDto;
  roleAssignmentChanged: boolean;

  roleForm: FormGroup;

  constructor(private router: Router,
              private route: ActivatedRoute,
              private roleService: RoleService,
              private core: CoreService,
              private formBuilder: FormBuilder) {
  }

  ngOnInit(): void {
    this.core.setTranslatedNavigateText('frigg.roles');
    this.core.shell.setNavigateUrl('/admin/roles');

    this.roleForm = this.formBuilder.group({
      displayName: [ '', Validators.required ],
      isDefault: [ false ]
    });

    this.roleForm.valueChanges.subscribe(() => {
      Object.assign(this.role, this.roleForm.getRawValue());
      this.setHeader();
    });

    this.route.data.subscribe((data: any) => {
      this.role = data.role;
      this.roleForm.patchValue(this.role, {onlySelf: true});
      this.setHeader();
    });

    this.roleAssignmentChanged = false;
  }

  setHeader(): void {
    this.core.shell.setHeader(this.role.displayName);
  }

  get canSave(): boolean {
    return this.roleForm.valid && this.roleForm.dirty;
  }

  onDeleteClick(): void {
    if (this.role.id !== 0) {
      this.core.dialog.confirm(this.core.translate('frigg.deleteRoleConfirmation', {roleName: this.role.displayName}))
        .subscribe((confirm: boolean) => {
          if (confirm) {
            this.roleService.deleteRole(this.role.id)
              .subscribe((response: FriggResponse<any>) => {
                if (response.success) {
                  this.router.navigate([ 'admin/roles' ], {replaceUrl: true});
                  this.core.toast.success(this.core.translate('frigg.roleDeleted'));
                }
              });
          }
        });
    }
  }

  onSaveClick(): void {
    const isNew = this.role.id === 0;

    const successMessage: string = isNew ?
      this.core.translate('frigg.roleCreated') :
      this.core.translate('frigg.roleUpdated');

    const callServer: Observable<FriggResponse<RoleDto>> =
      isNew ? this.roleService.createRole(this.role)
        : this.roleService.updateRole(this.role);

    callServer
      .subscribe((response: FriggResponse<RoleDto>) => {
        if (response.success) {
          this.role = response.result;
          this.core.toast.success(successMessage);
          this.roleForm.markAsPristine();
          if (isNew) {
            this.router.navigate([ 'admin/roles/' + this.role.id ], {replaceUrl: true});
          }
        } else {
          this.core.toast.error(response.error.details, response.error.message);
        }
      });
  }
}



