import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { CoreService } from '../../core/core.service';
import { ValidationService } from '../../core/validation/validation.service';
import { AssignedRoleDto, FriggResponse, UserDto, UserService } from '../../proxies';

@Component({
  selector: 'frigg-user-detail',
  templateUrl: 'user-detail.component.html',
  styleUrls: [ 'user-detail.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class UserDetailComponent implements OnInit {
  user: UserDto;
  isNew: boolean;
  roleAssignmentChanged: boolean;

  userForm: FormGroup;

  constructor(private router: Router,
              private route: ActivatedRoute,
              private core: CoreService,
              private userService: UserService,
              private formBuilder: FormBuilder) {
  }

  ngOnInit(): void {
    this.core.setTranslatedNavigateText('frigg.users');
    this.core.shell.setNavigateUrl('/admin/users');

    this.userForm = this.formBuilder.group({
      userName: [ '', Validators.required ],
      name: [ '', Validators.required ],
      surname: [ '', Validators.required ],
      emailAddress: [ '', [ Validators.required, ValidationService.emailValidator ] ],
      isActive: [ true ],
      password: [ '' ]
    });

    this.userForm.valueChanges.subscribe(() => {
      Object.assign(this.user, this.userForm.getRawValue());
      this.setHeader();
    });

    this.route.params.forEach((param: { id: number | string }) => {
      if (param.id === 'new') {
        this.isNew = true;
        this.userForm.controls[ 'password' ].setValidators(Validators.required);
        this.loadUser(0);
      } else {
        this.isNew = false;
        this.loadUser(param.id as number);
      }
    });

    this.roleAssignmentChanged = false;
  }

  get canSave(): boolean {
    return this.userForm.valid && (this.userForm.dirty || this.roleAssignmentChanged);
  }

  loadUser(id: number): void {
    this.userService.getUser(id)
      .subscribe((response: FriggResponse<UserDto>) => {
        if (response.success) {
          this.user = response.result;
          this.userForm.patchValue(this.user, {onlySelf: true});
          this.setHeader();

          if (this.user.userName === 'admin') {
            this.userForm.controls[ 'userName' ].disable();
          } else {
            this.userForm.controls[ 'userName' ].enable();
          }
        }
      });
  }

  setHeader(): void {
    this.core.shell.setHeader(
      this.user ?
        `${ this.user.name } ${ this.user.surname }` :
        '');
  }

  onDeleteClick(): void {
    if (!this.isNew) {
      this.core.dialog.confirm(this.core.translate('frigg.deleteUserConfirmation', {userName: this.user.userName}))
        .subscribe((confirm: boolean) => {
          if (confirm) {
            this.userService.deleteUser(this.user.id)
              .subscribe((response: FriggResponse<any>) => {
                if (response.success) {
                  this.router.navigate([ 'admin/users' ], {replaceUrl: true});
                  this.core.toast.success(this.core.translate('frigg.userDeleted'));
                }
              });
          }
        });
    }
  }

  onSaveClick(): void {
    const successMessage: string = this.isNew ?
      this.core.translate('frigg.userCreated') :
      this.core.translate('frigg.userUpdated');

    const callServer: Observable<FriggResponse<UserDto>> =
      this.isNew ? this.userService.createUser(this.user)
        : this.userService.updateUser(this.user);

    callServer.subscribe((response: FriggResponse<UserDto>) => {
        if (response.success) {
          this.user = response.result;
          this.core.toast.success(successMessage);
          this.userForm.markAsPristine();
          if (this.isNew) {
            this.router.navigate([ 'admin/users/' + this.user.id ], {replaceUrl: true});
          }
        } else {
          this.core.toast.error(response.error.details, response.error.message);
        }
      }
    );
  }


  onRoleCheckboxClick(role: AssignedRoleDto, checked: boolean): void {
    role.assigned = checked;
    this.roleAssignmentChanged = true;
  }
}



