import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RoleResolverService } from './role-detail/resolver/role-resolver.service';
import { RoleDetailComponent } from './role-detail/role-detail.component';
import { RoleListResolverService } from './role-list/resolver/role-list-resolver.service';
import { RoleListComponent } from './role-list/role-list.component';
import { UserDetailComponent } from './user-detail/user-detail.component';
import { UserListComponent } from './user-list/user-list.component';

const routes: Routes = [
  {path: 'users', component: UserListComponent, data: {title: 'Users'}},
  {path: 'users/:id', component: UserDetailComponent, data: {title: 'User'}},
  {path: 'roles', component: RoleListComponent, data: {title: 'Roles'}, resolve: {roles: RoleListResolverService}},
  {path: 'roles/:id', component: RoleDetailComponent, data: {title: 'Role'}, resolve: {role: RoleResolverService}},
];

@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ],
})
export class AdminRoutingModule {
}

export const routedComponents: Array<any> = [ UserListComponent, UserDetailComponent ];



