import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import 'rxjs/add/operator/filter'
import { Observable } from 'rxjs/Observable';
import { FriggResponse, ListResultDto, RoleListDto, RoleService } from '../../../proxies';

@Injectable()
export class RoleListResolverService implements Resolve<Array<RoleListDto>> {
  constructor(private roleService: RoleService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<RoleListDto[]> {
    return this.roleService.getRoleList()
      .filter((response: FriggResponse<ListResultDto<RoleListDto>>) => {
        return response.success;
      })
      .map((response: FriggResponse<ListResultDto<RoleListDto>>) => {
        return response.result.items;
      });
  }
}



